* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html, body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  width: 100%;
  font-family: 'Poppins', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  font-family: var(--default-font), -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

body{
  background-color: #FFFEFB;
  background-color: var(--white-color);
}

:root {
  --white-color: #FFFEFB;
  --black-color:  #1E1E1E;
  --default-font: 'Poppins';
  --side-space: 5%;
}

/* poppins-regular - latin */
@font-face {
  font-family: 'Poppins';
  font-style: normal;
  font-weight: 400;
  src: url(/test-react/static/media/poppins-v20-latin-regular.9bb84f43.eot); /* IE9 Compat Modes */
  src: local(''),
       url(/test-react/static/media/poppins-v20-latin-regular.9bb84f43.eot?#iefix) format('embedded-opentype'), 
       url(/test-react/static/media/poppins-v20-latin-regular.9212f6f9.woff2) format('woff2'), 
       url(/test-react/static/media/poppins-v20-latin-regular.f8ed3280.woff) format('woff'), 
       url(/test-react/static/media/poppins-v20-latin-regular.891f6ba4.ttf) format('truetype'), 
       url(/test-react/static/media/poppins-v20-latin-regular.c26cc61a.svg#Poppins) format('svg'); /* Legacy iOS */
}

section {
  padding: 5%;
  padding: var(--side-space);
  min-height: 50vh;
}

.menu-nav {
  width: 100%;
  position: absolute;
  display: flex;
  justify-content: space-between;
  top: 0;
  z-index: 1;
  padding: 1rem 5%;
  padding: 1rem var(--side-space);
}
.menu-nav .menu-bar.remove{
  display: none;
}

h2{
  font-size: 2.5rem;
  line-height: 125%;
  padding: 1rem 0;
}

.top-section {
  position: relative;
  min-height: 100vh;
  display: flex;
  align-items: center;
  color: #FFFEFB;
  color: var(--white-color);
  text-align: center;
}
.top-section > img{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
}
.page-intro h2 {
  padding: 3rem 0;
}
.page-intro p {
  line-height: 25px;
  word-spacing: 2px;
}

.links {
  position: fixed;
  padding: 3rem 0 0;
  top: 0;
  left: 100vw;
  width: 100%;
  height: 100vh;
  background-color: #1E1E1E;
  background-color: var(--black-color);
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  flex-wrap: wrap;
  z-index: -1;
  align-items: center;
  align-content: center;
}
.links a {
  font-size: 2rem;
  color: #FFFEFB;
  color: var(--white-color);
  text-decoration: none;
}


.summary-section {
  text-align: center;
  color: #1E1E1E;
  color: var(--black-color);
}



.summary-container {
  padding: 2rem 0;
}
.summary {
  width: 100%;
  padding: 2rem 0;
}
.summary .sum-num {
  font-size: 500%;
}
.summary .sum-img {
  width: 100%;
  height: 170px;
  margin-top: -80px;
}
.summary .sum-img img {
  height: 100%;
}
.summary .sum-topic {
  width: 100%;
  font-size: 120%;
}
.summary .sum-topic p:first-of-type {
  font-size: 85%;
}
.Box {
    width: 100%;
}
.images {
    display: flex;
    align-content: center;
    justify-content: center;
    align-items: center;
    height: 80vh;
    position: relative;
}
.images img {
    position: absolute;
    max-width: 50vw;
}
.images img.left {
    -webkit-transform: translateX(-12vw);
            transform: translateX(-12vw);
    max-height: 65%;
}
.images img.right {
    -webkit-transform: translateX(12vw);
            transform: translateX(12vw);
    max-height: 100%;
}
